
:root {
  --BackgroundHue: 130;
  --BackgroundSaturation: 10%;
  --BackgroundLightness: 10%;
}

*{
  color: hsla(0, 0%, 100%, 0.9);
  box-sizing: border-box;
  margin: auto;
  font-family: "Fira Code", monospace;
  text-decoration: none;
  line-height: 1.4;
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  padding: 0;
}

/*Header css start*/
header h1 {
  text-align: center;
  font-style: italic;
}
/*Header css end*/

/*menuItem css start*/
.navBar {
  display: flex;
  justify-content: center;
  align-content: center;
}

.MenuLink{
  color: white;
}

.MenuLink:hover {
  color: grey;
  transition: all ease-in-out;
  cursor: pointer;
}

.navBar li {
  font-size: 1.25rem;
  display: inline-block;
  margin-right: 80px;
  margin-left: 80px;
}
/*menuItem css end*/

/*home css start*/
.Home img {
  border-radius: 1.25rem;
  margin-right: 1%;
}

.Home {
 display: flex;
 flex-wrap: wrap;
 margin-top: 100px;
 font-size: 24px;
}

.Home p {
  max-width: 50%;
  text-size-adjust: 100%;
  margin-left: 0%;
}

.CV img {
  display: flex;
  align-items: center;
  margin-top: 50px;
  max-width: 30%;
}
/*home css end*/


/*Projectspreview.jsx css start--->*/
.ProjectsPreviewContainer {
  display: grid;
  grid-template-columns: 35% 35%;
  row-gap: 15px;
  column-gap: 20px;
  justify-content: center;
  margin-top: 50px;
  max-width: 65%;
}

.ProjectPreview {
  transition: all 0.3s ease-in-out;
  margin: 0 auto;
  cursor: pointer;
}

.ProjectPreview img {
  max-width: 100%;
  height: 250px;
  object-fit: cover;
}

.ProjectPreview:hover {
  transform: scale(1.05);
  transition: all 0.3s ease-in-out;
}

.ProjectPreview h3,.ProjectPreview p {
  color: white;
}
/*<--Projectpreview.jsx css end*/


.Project {
  display: grid;
  justify-content: center;
  font-size: 18px;
  max-width: 60%;
}

.Project h1,.Project h2,.Project h3,.Project h4,.Project h5,.Project h6,.Project body {
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  color: white;
  margin: auto;
  font-family:'nixie_oneregular', 'Courier New', Courier, monospace;
  text-size-adjust: 100%;
  margin-top: 20px;
}

.Project h1,.Project h2,.Project h3 {
  border-bottom: 2px solid currentColor;
}

.Project img {
  display: flex;
  justify-content: center;
  max-width: 100%;
  height: 350px;
  object-fit: cover;
  padding-top: 10px;
  padding-bottom: 10px;
}

.Project #bigImage {
  width: 100% !important;
  height: 100% !important;
  max-width: 50% !important;
}

.Project #centerize {
  display: flex;
  justify-content: center;
}

/*Accordion css start ->*/
.card{
  background-color: hsl(var(--BackgroundHue),var(--BackgroundSaturation),var(--BackgroundLightness));
  border: transparent;
  display: block !important;
}

.card-header {
  padding: .75rem 1.25rem;
  background-color: rgb(66, 66, 66);
  color: white;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  font-size: 24px;
}

.card-body {
  color: transparent;
  background-color: transparent;
}
/*<- Accordion css end*/

footer{
  bottom: 0;
  width: 100%;
  padding-top: 50px;
  text-align: center;
  font-size: 9pt;
}